import React from 'react';
import { window, autoFlex } from './Window.module.scss';
import classnames from 'classnames';

const Window = ({ children, isAutoFlex, className }) => {

  return (
    <div className={classnames(window, isAutoFlex ? autoFlex : null, className)}>
      {children}
    </div>
  )
};

export default Window;
