import React, { useState } from 'react';
import { app } from './App.module.scss';
// Modules
import 'normalize.css'
import { useRoutes, navigate } from 'hookrouter';
// Base Layout
import { ConsoleLayout } from './layouts';
// Page Imports
import { Menu } from './pages';
import { Intro } from './pages'
import { About } from './pages'
import { Projects } from './pages'
import { NotFound } from './pages';


function App() {
  // App page/content state
  const [pageIdx, setPageIdx] = useState(0)
  const [subpageIdx, setSubpageIdx] = useState(0)
  const [menuActive, setMenuActive] = useState(false)
  //Easter egg on Intro screen
  const [highScore, setHighScore] = useState(0)
  // Menu handlers
  const handleClick = (i,j) => {
    setPageIdx(j);
    navigate(i);
    setSubpageIdx(0);
    showMenu();
  }
  const showMenu = () => {
    setMenuActive(!menuActive);
    setHighScore(highScore + 100);
  }
  // Page navigation handlers
  const nextSubpage = () => {
    if (!menuActive) {
      var pageId = pageIdx;
      var subpageId = subpageIdx;
      if (checkLanding()) {
        pageId = routeResult.props.id;
        subpageId = routeResult.props.subpage;
      }
      setSubpageIdx(subpageId + 1);
      if (paths[pageId].length-1 <= subpageId) {
        setSubpageIdx(0)
        navigate(paths[pageId][0]);
      } else {
      navigate(paths[pageId][subpageId+1]);
      }
    }
    setHighScore(highScore + 100);
  }
  const prevSubpage = () => {
    if (!menuActive) {
      var pageId = pageIdx;
      var subpageId = subpageIdx;
      if (checkLanding()) {
        pageId = routeResult.props.id;
        subpageId = routeResult.props.subpage;
      }
      if (subpageId-1 < 0) {
        setSubpageIdx(paths[pageId].length-1);
        navigate(paths[pageId][paths[pageId].length-1]);
      } else {
      setSubpageIdx(subpageId - 1);
      navigate(paths[pageId][subpageId-1]);
      }
    }
    setHighScore(highScore + 100);
  }
  // check if user landed on non-root page and sync page state with app
  const checkLanding = () => {
    var newLanding = false;
    if (routeResult == null) {
      return newLanding;
    }
    if (routeResult.props.id !== pageIdx) {
      setPageIdx(routeResult.props.id);
      newLanding = true;
    }
    if (routeResult.props.subpage !== subpageIdx) {
      setSubpageIdx(routeResult.props.subpage);
      newLanding = true;
    }
    return newLanding;
  }

  // Used to generate Menu list
  const pages = [
    { name: 'Home', component: '/' , id: 0},
    { name: 'Projects', component: '/project', id: 1 },
    { name: 'About', component: '/about', id: 2 },
  ]
  
  // Routes required by hookrouter
  const routes = {
    '/' : () => <Intro id={0} subpage={0} highScore={highScore}/>,
    '/navhint' : () => <Intro id={0} subpage={1} />,
    '/project' : () => <Projects id={1} subpage={0}/>,
    '/project/spacesurvivor' : () => <Projects id={1} subpage={1}/>,
    '/project/chiptune' : () => <Projects id={1} subpage={2}/>,
    '/project/pebble' : () => <Projects id={1} subpage={3}/>,
    '/about': () => <About id={2} subpage={0}/>,
    '/about/website' : () => <About id={2} subpage={1}/>
  };

  // Route paths in array form for page navigation handlers
  const paths = [
    ['/', '/navhint'],
    ['/project', '/project/spacesurvivor', '/project/chiptune', '/project/pebble'],
    ['/about', '/about/website']
  ]

  // Debug array bounds
  // console.log(paths[pageIdx][subpageIdx]);
  // console.log(pageIdx);
  // console.log(subpageIdx);

  const routeResult = useRoutes(routes);
  // console.log(routeResult.type.name);
  // console.log(routeResult.props.id);

  return (
    <div className={app}>
      <ConsoleLayout showMenu={showMenu} nextSubpage={nextSubpage} prevSubpage={prevSubpage}>
        {menuActive && <Menu handleClick={handleClick} pages={pages}/> }
        {/* {!menuActive && pages[pageIdx].component} */}
        {!menuActive && ((routeResult) || <NotFound />)}
      </ConsoleLayout>
    </div>
  );
}

export default App;
