import React from 'react';
import { consoleLayout } from './ConsoleLayout.module.scss';
import classnames from 'classnames';

const ConsoleLayout = ({ children, showMenu, nextSubpage, prevSubpage }) => {
  return (
    <div className={consoleLayout}>
      <div className="consoleChildren">
        <div className="console">
          <div className={classnames('consoleBezel', 'consoleTopBezel')}>
            <i>S.G. Productions</i>
          </div>
          <div className="consoleScreen">
            <div className="frameChildren">{ children }</div>
          </div>
          <div className={classnames('consoleBezel', 'consoleBottomBezel')}>
            <button className={classnames('consoleButton', 'consoleButtonM')} onClick={showMenu} alt="Button" />
            Menu
            <button className={classnames('consoleButton', 'consoleButtonA')} onClick={prevSubpage} alt="Button" />
            <button className={classnames('consoleButton', 'consoleButtonB')} onClick={nextSubpage} alt="Button" />
          </div>
        </div>
      </div>
    </div>
  )
}

export default ConsoleLayout;
