import React from 'react';
import { intro } from './Intro.module.scss';
import { IntroTop } from '../../pages';
import { IntroSub } from '../../pages';

const Intro= ({ subpage, highScore }) => {

  const subpages = [
    { name: 'Intro', component: <IntroTop highScore={highScore}/> },
    { name: 'IntroSub', component: <IntroSub /> }
  ]

  return (
    <div className={intro}>
      {subpages[subpage].component}
    </div>
  )
};

export default Intro;
